import React, { useState } from "react";
import { MdCheckCircle, MdClose, MdRadioButtonUnchecked } from "react-icons/md";
import { DistrictTypes } from "../../../types/TransferTypes";
import { search } from "../../../utils/functions";

interface DistrictsLabelProps {
  onChange: (value: string) => void;
  districts: DistrictTypes[];
  selected_district: string;
  setVisibility: (status: boolean) => void;
}

const DistrictsLabel = (props: DistrictsLabelProps) => {
  const [searchData, setSearchData] = useState<string>("");
  return (
    <div
      className="absolute animate__animated animate__fadeIn animate__faster"
      style={{ width: "500px" }}
    >
      <div className="bg-white rounded shadow-xl mb-10">
        <div className="p-3 flex flex-row items-center justify-between gap-3">
          <input
            type="search"
            value={searchData}
            onChange={(e) => setSearchData(e.target.value)}
            placeholder="Type district name..."
            className="bg-white rounded px-3 py-2 border border-primary-800 w-full"
          />
          <div onClick={() => props.setVisibility(false)}>
            <div className="h-8 w-8 rounded-full bg-red-100 hover:bg-red-200 flex items-center justify-center">
              <MdClose className="text-red-600 text-2xl cursor-pointer" />
            </div>
          </div>
        </div>
        <div className="max-h-80 overflow-y-auto pb-3">
          {(search(props.districts, searchData) as DistrictTypes[]).length ===
          0 ? (
            <div className="text-gray-500 font-extrabold text-xl text-center p-4">
              No data found!
            </div>
          ) : (
            (search(props.districts, searchData) as DistrictTypes[]).map(
              (item, i) => (
                <div
                  onClick={() => {
                    props.onChange(item.district_code);
                    props.setVisibility(false);
                  }}
                  className={`border-b py-2 px-3 group ${
                    props.selected_district === item.district_code
                      ? "bg-blue-200 font-extrabold text-primary-800"
                      : ""
                  } hover:bg-primary-800 hover:text-white cursor-pointer hover:border-blue-500`}
                  key={i + 1}
                >
                  <div className="flex flex-row items-center gap-4">
                    <div>
                      {props.selected_district === item.district_code ? (
                        <MdCheckCircle className="text-2xl" />
                      ) : (
                        <MdRadioButtonUnchecked className="text-2xl" />
                      )}
                    </div>
                    <div>{item.district_name}</div>
                  </div>
                </div>
              )
            )
          )}
        </div>
      </div>
    </div>
  );
};

export default DistrictsLabel;
