import React, { Component, Fragment } from "react";
import { IoIosArrowDown, IoIosArrowUp } from "react-icons/io";
import { connect } from "react-redux";
import { Redirect } from "react-router";
import {
  AcademicDDEDistribution,
  API_DistributedDDE,
  Auth,
  Basic,
  FC_GetAllPositions,
  FC_GetDistributionSchools,
  FC_GetWaitingList,
  FC_RequestTeachersInTRS_NEW,
  RequestInTRS,
  ReturnedCandidates,
  WaitingList,
} from "../../actions";
import { FC_GetDistricts } from "../../actions/transfer.action";
import Button from "../../components/Buttons/Button";
import DashboardContainer from "../../components/DashboardContainer/DashboardContainer";
import Heading from "../../components/Heading/Heading";
import Loading from "../../components/Loading/Loading";
import TableLoading from "../../components/TableLoading/TableLoading";
import { StoreState } from "../../reducers";
import { DistrictTypes } from "../../types/TransferTypes";
import { checkAccess, UsersAccess } from "../../utils/access";
import { isEmptyOrSpaces, shortString } from "../../utils/functions";
import DistrictsLabel from "../Reports/PostsRequestsReport/DistrictsLabel";
import SelectPosition from "./SelectPosition";

interface DoPlacementProps {
  auth: Auth;
  basic: Basic;

  FC_GetAllPositions: Function;
}

interface DoPlacementStates {
  loading: boolean;
  loadingDistribution: boolean;
  loading_candidates_from_trs: boolean;
  districts: DistrictTypes[] | null;
  showDistrictsList: boolean;
  selected_district: string;
  showPositionList: boolean;
  selected_position: string;
  distributionList: API_DistributedDDE | null;
  searchData: string;
  error: {
    target:
      | "requests-error"
      | "loading-update-error"
      | "main"
      | "main-success"
      | "main-error"
      | "update-error";
    msg: string;
  } | null;
  selected_request_id: string;
  new_value: string;
  customVacant: { selected_request_id: string; new_value: string }[];
  waiting_list_total: WaitingList[] | null;
  collected_teachers: ReturnedCandidates[];
  collected_candidates_open: boolean;
}

class _DoPlacement extends Component<DoPlacementProps, DoPlacementStates> {
  constructor(props: DoPlacementProps) {
    super(props);

    this.state = {
      loading: true,
      loadingDistribution: false,
      loading_candidates_from_trs: false,
      districts: null,
      showDistrictsList: false,
      selected_district: "",
      showPositionList: false,
      selected_position: "",
      distributionList: null,
      searchData: "",
      error: null,
      selected_request_id: "",
      new_value: "0",
      customVacant: [],
      waiting_list_total: null,
      collected_teachers: [],
      collected_candidates_open: false,
    };
  }

  componentDidMount() {
    // Get Districts Function
    if (
      this.props.auth.role &&
      this.props.auth.role.role_id.toString() === "4"
    ) {
      FC_GetDistricts(
        (status: boolean, data: DistrictTypes[] | null, msg: string) => {
          this.setState({ loading: status });
          if (data !== null && status === false) {
            this.setState({
              loading: false,
              districts: data,
            });
          }
          // else if (status === false) {
          //   this.setState({
          //     loading: false,
          //   });
          // }
        }
      );

      if (this.props.basic.positions === null)
        this.props.FC_GetAllPositions((status: boolean, msg: string) => {
          this.setState({ loading: status });
        });

      if (
        this.state.waiting_list_total === null ||
        this.state.waiting_list_total.length === 0
      )
        FC_GetWaitingList(
          (status: boolean, data: WaitingList[] | null, msg: string) => {
            this.setState({ loading: true });
            if (status === true) {
              this.setState({
                loading: false,
                waiting_list_total: data,
              });
            } else if (status === false) {
              this.setState({
                loading: false,
              });
            }
          }
        );
    }
  }

  FC_GetDistrictDistribution = (district_id: string) => {
    if (this.props.basic.academic_calender)
      FC_GetDistributionSchools(
        {
          academic_id: this.props.basic.academic_calender.academic_year_id,
          district_id: district_id,
        },
        (status: boolean, data: API_DistributedDDE | null, msg: string) => {
          this.setState({ loadingDistribution: true, distributionList: null });
          if (data !== null && status === true) {
            this.setState({
              loadingDistribution: false,
              distributionList: data,
            });
          } else if (status === true) {
            this.setState({
              loadingDistribution: false,
            });
          }
        }
      );
  };

  FC_ChangeCustomVacant = (default_value: number, new_value: string) => {
    let custom_vacant_copy = [...this.state.customVacant];
    let thisVacantCustom = custom_vacant_copy.findIndex(
      (item) => item.selected_request_id === this.state.selected_request_id
    );
    const new_value_copy: string =
      isEmptyOrSpaces(new_value) || Number(new_value) < 0 ? "0" : new_value;
    if (thisVacantCustom >= 0) {
      custom_vacant_copy[thisVacantCustom] = {
        selected_request_id: this.state.selected_request_id,
        new_value:
          Number(new_value_copy) > Number(default_value)
            ? default_value.toString()
            : new_value_copy,
      };
    } else {
      custom_vacant_copy.push({
        selected_request_id: this.state.selected_request_id,
        new_value:
          Number(new_value_copy) > Number(default_value)
            ? default_value.toString()
            : new_value_copy,
      });
    }

    console.log("================new_value_copy====================");
    console.log({ new_value_copy });
    console.log("================new_value_copy====================");

    this.setState({
      customVacant: custom_vacant_copy,
      selected_request_id: "",
      new_value: "",
    });
  };

  FC_GetDistributionValue = (data: AcademicDDEDistribution): string => {
    let result = this.state.customVacant.find(
      (item) => item.selected_request_id === data.post_request_id
    );
    if (result) return result.new_value;
    return data.dde_post_distribution.toString();
  };

  FC_FilterByPosition = (
    data: API_DistributedDDE | null,
    position_code: string
  ): AcademicDDEDistribution[] => {
    return data
      ? data.academic_distributed.filter(
          (item) => item.position_code.toString() === position_code.toString()
        )
      : [];
  };

  FC_CalculateRequests = (data: AcademicDDEDistribution[]): number => {
    let result: number = 0;

    data.forEach((itemDistributed) => {
      result += Number(this.FC_GetDistributionValue(itemDistributed));
    });

    return result;
  };

  FC_GetRequestsForTRS = (data: AcademicDDEDistribution[]): RequestInTRS[] => {
    let distributed: number = 0,
      requestArray: RequestInTRS[] = [];

    data.forEach((itemDistributed) => {
      // CALCULATING NUMBER OF POST REQUEST ==========================
      distributed = Number(this.FC_GetDistributionValue(itemDistributed));
      requestArray.push({
        post_request_id: itemDistributed.post_request_id,
        school_code: itemDistributed.school_code,
        district_code: itemDistributed.district_code,
        sector_code: itemDistributed.school_code.substring(0, 4),
        province_code: itemDistributed.school_code.substring(0, 1),
        gender: "",
        in_education: false,
        disability: false,
        academic_year_id: this.props.basic.academic_calender
          ? this.props.basic.academic_calender.academic_year_id
          : "",
        available_places: [
          {
            position_code: itemDistributed.position_code,
            requested_teacher: distributed,
          },
        ],
      });
      distributed = 0;
    });

    return requestArray;
  };

  submitDataRequestTRS = (data: AcademicDDEDistribution[]) => {
    const requestToSend = this.FC_GetRequestsForTRS(data);

    //
    this.setState({
      loading_candidates_from_trs: true,
      error: null,
    });
    FC_RequestTeachersInTRS_NEW(
      this.state.selected_position,
      requestToSend,
      (status, data, err) => {
        if (status === true && data !== null) {
          this.setState({
            loading_candidates_from_trs: false,
            error: {
              target: "main-error",
              msg: err,
            },
            collected_teachers: data,
            // loading_send_list_to_tmis: true,
            collected_candidates_open: true,
          });

          // this.SendTeachersInTMIS(data);
        } else {
          this.setState({
            loading_candidates_from_trs: false,
            error: {
              target: "main-error",
              msg: err,
            },
          });
        }
      }
    );
  };

  render() {
    // Check access
    let access_list = this.props.auth.role?.access;
    if (checkAccess(access_list, UsersAccess.DO_PLACEMENT) === false) {
      return <Redirect to="/dashboard" />;
    }

    if (this.state.loading === true) {
      return (
        <DashboardContainer className="flex flex-col items-center justify-center">
          <Loading />
          <div>Loading, please wait... {this.props.auth.role?.role_id}</div>
        </DashboardContainer>
      );
    }

    let selected_district_name = this.state.districts?.find(
      (itm) => itm.district_code === this.state.selected_district
    );

    let selected_position = this.props.basic.positions?.find(
      (itm) => itm.position_code === this.state.selected_position
    );

    const DATA_Result: AcademicDDEDistribution[] = this.FC_FilterByPosition(
      this.state.distributionList,
      this.state.selected_position
    );

    const DATA_TotalRequests: number = this.FC_CalculateRequests(DATA_Result);

    const DATA_waitingList: WaitingList | undefined = this.state
      .waiting_list_total
      ? this.state.waiting_list_total.find(
          (item) =>
            item._id.toString() === this.state.selected_position.toString()
        )
      : undefined;

    // Render the list
    if (this.state.collected_candidates_open === true)
      return (
        <DashboardContainer className="animate__animated animate__zoomIn animate__faster">
          <div className="flex border-b p-2">
            <div className="flex-1 text-center">
              <Heading size="md">
                List of teachers form TRS waiting list
              </Heading>
            </div>
          </div>
          <div className="p-2">
            <div className="grid grid-cols-2 gap-3 w-full">
              {[
                {
                  title: "Position",
                  value: `(${selected_position?.position_code}) ${selected_position?.position_name}`,
                  special: false,
                },
                {
                  title: "Total vacant post",
                  value: DATA_TotalRequests,
                  special: false,
                },
              ].map((itm, i) => (
                <div
                  key={i}
                  className={`bg-gray-50 py-1 px-2 border rounded flex-1 `}
                >
                  <span className="text-gray-500 block">{itm.title}:</span>{" "}
                  <b>{itm.value}</b>
                </div>
              ))}
            </div>
          </div>
          <div className="p-2">
            <table className="w-full">
              <thead>
                <tr>
                  <th className="p-2 border">#</th>
                  <th className="p-2 border">NID</th>
                  <th className="p-2 border">Names</th>
                  <th className="p-2 border">Phone</th>
                  <th className="p-2 border">Email</th>
                  <th className="p-2 border">Gender</th>
                  <th className="p-2 border">School</th>
                </tr>
              </thead>
              <tbody>
                {this.state.collected_teachers.length <= 0 ? (
                  <tr>
                    <td colSpan={6} className="py-5 text-center">
                      <Heading>No candidates found!</Heading>
                      <p>No results found</p>
                    </td>
                  </tr>
                ) : (
                  this.state.collected_teachers.map((cand, i) => {
                    const school = DATA_Result.find(
                      (item) =>
                        item.school_code.toString() ===
                        cand?.school_code.toString()
                    );
                    return (
                      <tr key={i}>
                        <td className="p-2 border">{i + 1}</td>
                        <td className="p-2 border">{cand?.user_info?.n_id}</td>
                        <td className="p-2 border">
                          {cand?.user_info?.user_info?.fname}{" "}
                          {cand?.user_info?.user_info?.lname}
                        </td>
                        <td className="p-2 border">
                          {cand?.user_info?.phone_number}
                        </td>
                        <td className="p-2 border">{cand?.user_info?.email}</td>
                        <td className="p-2 border">
                          {cand?.user_info?.user_info?.sex}
                        </td>
                        <td className="p-2 border">
                          {school
                            ? `${school.school_code} - ` + school.school_name
                            : ""}
                        </td>
                      </tr>
                    );
                  })
                )}
              </tbody>
            </table>
            <div className="p-2 text-right">
              <Button
                onClick={() =>
                  this.setState({
                    collected_candidates_open: false,
                    collected_teachers: [],
                    customVacant: [],
                    selected_position: "",
                  })
                }
                theme="primary-light"
                className="mt-1"
              >
                Add teacher to others position / district
              </Button>
            </div>
          </div>
        </DashboardContainer>
      );

    return (
      <>
        {!isEmptyOrSpaces(this.state.selected_district.toString()) &&
          !isEmptyOrSpaces(this.state.selected_position.toString()) &&
          DATA_waitingList && (
            <div className="fixed bottom-0 right-0 flex left-0 bg-white shadow-lg border-t-2 px-3 items-center">
              <div className="main-side-spacer"></div>
              <div className="flex-1 p-2 flex justify-between items-center">
                <div className="flex items-center">
                  <div className="px-2 bg-gray-50 rounded">
                    <span className="text-sm">Waiting list</span>
                    <h3 className="font-bold text-2xl -mt-1">
                      {DATA_waitingList.waiting}
                    </h3>
                  </div>
                  <div
                    className={`mr-4 bg-green-50 ${
                      DATA_waitingList.waiting >= DATA_TotalRequests &&
                      DATA_TotalRequests > 0
                        ? "text-green-700"
                        : "text-red-700"
                    } px-2`}
                  >
                    <span className="text-sm">Total requests</span>
                    <h3 className="font-bold text-2xl -mt-1">
                      {DATA_TotalRequests}
                    </h3>
                  </div>
                </div>
                <div className="">
                  {DATA_waitingList.waiting >= DATA_TotalRequests &&
                  DATA_TotalRequests > 0 ? (
                    <Button
                      theme="accent"
                      onClick={() => this.submitDataRequestTRS(DATA_Result)}
                      loading={this.state.loading_candidates_from_trs}
                      className="animate__animated animate__zoomIn animate__faster"
                    >
                      Do placement
                    </Button>
                  ) : (
                    <span className="text-red-500">
                      {DATA_TotalRequests === 0
                        ? "Vacant post is zero!"
                        : "Vacant post is greater than waiting list!"}
                    </span>
                  )}
                </div>
              </div>
            </div>
          )}

        <DashboardContainer>
          {/* HEADER TITLE ============== */}

          <div className="flex items-start">
            <div className="flex-1 mb-3">
              {/* <Heading>Do placement</Heading> */}
              <div className="text-primary-800 text-2xl font-extrabold mb-2">
                Do placement
              </div>
              <Heading size="sm" theme="accent">
                <b>
                  {selected_district_name
                    ? `${selected_district_name.district_name}`
                    : ""}
                  {selected_position
                    ? ` / ${selected_position.position_name}`
                    : ""}
                </b>
              </Heading>
            </div>

            {/* {waitingList && (
              <div className="flex items-center">
                <div className="p-1 px-3 bg-blue-100 rounded mx-2">
                  <p className="text-sm">Waiting list</p>
                  <h2 className="text-2xl -mt-1 font-bold">
                    {isNaN(waitingList.waiting) ? "" : waitingList.waiting}
                  </h2>
                </div>
              </div>
            )} */}
          </div>

          {/* FILTER DATA ======================== */}

          <div className="flex flex-col sm:flex-row items-start gap-3 justify-between">
            {/* FILTER DISTRICT DATA ======================== */}
            <div className="flex flex-row items-center gap-3 w-full">
              {this.state.districts === null ? (
                <div className="font-bold">Loading, please wait...</div>
              ) : (
                <div className="w-full">
                  <div
                    onClick={() =>
                      this.setState({
                        showDistrictsList: !this.state.showDistrictsList,
                      })
                    }
                    className={`border font-bold ${
                      this.state.showDistrictsList === false
                        ? "border-gray-400 hover:border-primary-800 hover:bg-blue-100"
                        : "border"
                    } hover:text-primary-800 w-full rounded px-3 py-2 bg-white cursor-pointer flex flex-row items-center gap-3 justify-between`}
                  >
                    <div className={`flex flex-row items-center gap-3`}>
                      {this.state.selected_district !== "" &&
                        selected_district_name && (
                          <div className="rounded-full px-2 bg-blue-200 text-primary-800">
                            {shortString(selected_district_name.district_name)}
                          </div>
                        )}
                      <span>
                        {this.state.selected_district === ""
                          ? "Choose a district"
                          : "Change district"}
                      </span>
                    </div>

                    <div>
                      {this.state.showDistrictsList === false ? (
                        <IoIosArrowDown className="text-xl" />
                      ) : (
                        <IoIosArrowUp className="text-xl" />
                      )}
                    </div>
                  </div>
                  {this.state.showDistrictsList === true && (
                    <DistrictsLabel
                      onChange={(value: string) => {
                        this.setState({ selected_district: value });
                        this.FC_GetDistrictDistribution(value);
                      }}
                      districts={this.state.districts}
                      selected_district={this.state.selected_district}
                      setVisibility={(status: boolean) => {
                        this.setState({ showDistrictsList: status });
                      }}
                    />
                  )}
                </div>
              )}
            </div>

            {/* FILTER POSITION DATA ======================== */}
            <div className="flex flex-row items-center gap-3 w-full">
              {this.props.basic.positions === null ? (
                <div className="font-bold">Loading, please wait...</div>
              ) : (
                <div className="w-full">
                  <div
                    onClick={() =>
                      this.setState({
                        showPositionList: !this.state.showPositionList,
                      })
                    }
                    className={`border font-bold ${
                      this.state.showPositionList === false
                        ? "border-gray-400 hover:border-primary-800 hover:bg-blue-100"
                        : "border"
                    } hover:text-primary-800 w-full rounded px-3 py-2 bg-white cursor-pointer flex flex-row items-center gap-3 justify-between`}
                  >
                    <div className="flex flex-row items-center gap-3">
                      {this.state.selected_position !== "" &&
                        selected_position && (
                          <div className="rounded-full px-2 bg-blue-200 text-primary-800">
                            {shortString(selected_position.position_name)}
                          </div>
                        )}
                      <span>
                        {this.state.selected_position === ""
                          ? "Choose a position"
                          : "Change position"}
                      </span>
                    </div>

                    <div>
                      {this.state.showPositionList === false ? (
                        <IoIosArrowDown className="text-xl" />
                      ) : (
                        <IoIosArrowUp className="text-xl" />
                      )}
                    </div>
                  </div>
                  {this.state.showPositionList === true && (
                    <SelectPosition
                      onChange={(value: string) => {
                        this.setState({ selected_position: value });
                      }}
                      positions={this.props.basic.positions}
                      selected_position={this.state.selected_position}
                      setVisibility={(status: boolean) => {
                        this.setState({ showPositionList: status });
                      }}
                    />
                  )}
                </div>
              )}
            </div>
          </div>

          {/* BODY DATA ======================== */}
          <div>
            {(isEmptyOrSpaces(this.state.selected_district.toString()) ||
              isEmptyOrSpaces(this.state.selected_position.toString())) && (
              <div
                onClick={() =>
                  this.setState({
                    showDistrictsList: !this.state.showDistrictsList,
                  })
                }
                className="underline px-5 py-5 cursor-pointer rounded-md mt-5 bg-blue-50 text-gray-900 font-extrabold text-center w-full text-2xl"
              >
                {isEmptyOrSpaces(this.state.selected_district)
                  ? "No district selected!"
                  : "No position selected!"}
              </div>
            )}

            {/* {this.state.distributionList&&this.state.distributionList} */}
            {!isEmptyOrSpaces(this.state.selected_district.toString()) &&
              !isEmptyOrSpaces(this.state.selected_position.toString()) && (
                <div className="my-3">
                  <table className="w-full text-left table-auto">
                    <thead>
                      <tr>
                        <th className="p-2 border">#</th>
                        <th className="p-2 border">School name</th>
                        <th className="p-2 border">Post name</th>
                        <th className="p-2 border">
                          Existing post{"("}s{")"}
                        </th>
                        <th className="p-2 border ">Vacant posts</th>
                      </tr>
                    </thead>
                    <tbody>
                      {this.state.loadingDistribution ? (
                        <TableLoading cols={5} />
                      ) : this.state.distributionList === null ||
                        this.state.distributionList.academic_distributed
                          .length === 0 ||
                        DATA_Result.length === 0 ? (
                        <tr>
                          <td
                            colSpan={5}
                            className="p-6 border rounded border-opacity-25 animate__animated animate__fadeIn text-center"
                          >
                            <Heading size="md">No results found</Heading>
                          </td>
                        </tr>
                      ) : (
                        DATA_Result.map(
                          (itemDistributed: AcademicDDEDistribution, i) => {
                            let distributionValue =
                              this.FC_GetDistributionValue(itemDistributed);
                            return (
                              <Fragment key={i}>
                                <tr className="border rounded hover:bg-blue-500 hover:bg-opacity-20">
                                  <td className="p-2 border" key={i}>
                                    {i + 1}
                                  </td>
                                  <td className="p-2 border">
                                    {itemDistributed.school_name}
                                  </td>
                                  <td className="p-2 border">
                                    {itemDistributed.position_name}
                                  </td>
                                  <td className="p-2 border">
                                    {itemDistributed.existing_post}
                                  </td>

                                  {this.state.selected_request_id ===
                                  itemDistributed.post_request_id ? (
                                    <td className=" border  w-20 relative">
                                      <input
                                        type="number"
                                        className="w-full p-1 border-4 rounded-t border-primary-800 font-bold bg-transparent"
                                        value={this.state.new_value}
                                        onChange={(e) => {
                                          Number(e.target.value) < 0
                                            ? this.setState({
                                                new_value: "0",
                                              })
                                            : this.setState({
                                                new_value: e.target.value,
                                              });
                                        }}
                                        onBlur={(e) =>
                                          this.FC_ChangeCustomVacant(
                                            itemDistributed.dde_post_distribution,
                                            e.target.value
                                          )
                                        }
                                        max={
                                          itemDistributed.dde_post_distribution
                                        }
                                        autoFocus
                                        min={0}
                                      />
                                    </td>
                                  ) : (
                                    <td
                                      className="p-2 border w-20 font-bold bg-green-100 bg-opacity-40 text-green-800 cursor-pointer"
                                      onClick={() =>
                                        this.setState({
                                          selected_request_id:
                                            itemDistributed.post_request_id,
                                          new_value: distributionValue,
                                        })
                                      }
                                    >
                                      {distributionValue}
                                    </td>
                                  )}
                                </tr>
                              </Fragment>
                            );
                          }
                        )
                      )}
                    </tbody>
                  </table>
                </div>
              )}
          </div>
        </DashboardContainer>
      </>
    );
  }
}

const mapStateToProps = ({
  auth,
  basic,
}: StoreState): { auth: Auth; basic: Basic } => {
  return { auth: auth, basic: basic };
};
export const DoPlacement = connect(mapStateToProps, { FC_GetAllPositions })(
  _DoPlacement
);
